import { dateMixin } from 'common/js/mixin'

export default {
  name: 'plan-item',
  props: {
    item: {
      type: Object,
      default: ''
    }
  },
  mixins: [dateMixin],
  data () {
    return {}
  },
  computed: {},
  components: {},
  watch: {},
  methods: {
    modifyItem () {
      this.$emit('modifyPlan', this.item)
    },
    deleteItem () {
      this.$emit('deletePlan', this.item.id)
    },
    selectedItem () {
      this.$emit('selectedItem', this.item)
    }
  },
  created () {
  },
  mounted () {}
}



