import Block from 'base/block/block'
import Scroll from 'base/scroll/scroll'
import Loading from 'base/loading/loading'
import PlanItem from 'base/plan-item/plan-item'

export default {
  name: 'plan-list',
  mixins: [],
  props: {
    planList: {
      type: Array,
      default: []
    },
    recommendPlanList: {
      type: Array,
      default: []
    },
    // 增加推荐计划为空时loading条件
    isShowLoading: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      isLoading: false
    }
  },
  computed: {
    list () {
      const list = [...this.planList, ...this.recommendPlanList]
      return list
    }
  },
  components: {
    Block,
    Scroll,
    Loading,
    PlanItem
  },
  methods: {},
  created () {},
  mounted () {}
}



